import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function toastError(query) {
  return toast.error(`We did not find ${query}!`, {
    position: 'top-right',
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
}
